@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
.Sidebar__logo{
            width: 50%;
            margin-top: 20px;
            border-radius: 15px;
}
.Sidebar{
    flex: 0.4;
}
.Sidebar__name{
    font-family: 'Kaushan Script';
}

.Sidebar__surname{
    color: #00f260
}
.Sidebar >h3{
    background-color: #f1f1f1;
    border-radius: 30px;
    padding: 10px;
    margin: 10px;
}
.Sidebar__download{
    margin-bottom:6px;
    padding-top:6px;
    height:40px;
    font-size:1.1rem;
    border-radius:30px;
    margin-right: 12px;
    margin-left: 12px;
    padding: 1px;
     
} 
a{
    color: inherit;

}

.Sidebar__download > sup{
    font-size: large;
    text-decoration: none;
}

.Sidebar__download > img{
    width: 40px;
    border-radius: 30px;
}
.Sidebar__download:hover{
    background-color: #f1f1f1;  
    cursor: pointer;
} 
.Sidebar__social{
    margin-top: 15px;  
}
.Sidebar__contact{
    margin: 10px 10px;
    background-color: #dbdddf;
    padding: 1px;
    border-radius: 20px;
    justify-content: center;
    
}


.Sidebar__contact> p{
    margin-right: 25px;
}
.Sidebar__github{
    padding: 8px;
    border-radius: 20px;
    margin-top: 10px;
}
.Sidebar__github:hover{
    background-color: #f1f1f1;
    cursor: pointer;
    
}
.Sidebar__email{
    padding:10px;
    background-color: #f1f1f1;
    border-radius: 50px;
    margin: 20px 30px;
    background-image:linear-gradient(to right bottom,#00f260,#0575e6);
    cursor:pointer;
    transition:all 2s linear;
}
.Sidebar__email:hover{transform: scale(1.2)}


