
body{background-image:linear-gradient(to right bottom,#00f260,#0575e6);
background-attachment:fixed;color:#354538
}
.body{
    display: flex;
    padding: 30px 70px;
    height: 105vh;
}

::-webkit-scrollbar{
    width: 12px;
}

::-webkit-scrollbar-thumb{
    background-image: linear-gradient(to right bottom , #00F260 , #0575E6);
}

.sidebar{
    flex: 0.24;
    border-radius: 30px;
    text-align: center;
    background-color: white;
}

.rightbar{
    flex: 0.8;
    border-radius: 30px;
    margin-left: 20px;
    background-color: white;
}

@media (max-width: 600px) {
    .body {
        margin-right: 10px;
        margin-left: 10px;
    }

    .navbar_active {
        font-size: 1.5rem;
    }

    .navbar_item {
        font-size: 1rem;
    }
}