@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);


body{background-image:linear-gradient(to right bottom,#00f260,#0575e6);
background-attachment:fixed;color:#354538
}
.body{
    display: flex;
    padding: 30px 70px;
    height: 105vh;
}

::-webkit-scrollbar{
    width: 12px;
}

::-webkit-scrollbar-thumb{
    background-image: linear-gradient(to right bottom , #00F260 , #0575E6);
}

.sidebar{
    flex: 0.24 1;
    border-radius: 30px;
    text-align: center;
    background-color: white;
}

.rightbar{
    flex: 0.8 1;
    border-radius: 30px;
    margin-left: 20px;
    background-color: white;
}

@media (max-width: 600px) {
    .body {
        margin-right: 10px;
        margin-left: 10px;
    }

    .navbar_active {
        font-size: 1.5rem;
    }

    .navbar_item {
        font-size: 1rem;
    }
}
.Sidebar__logo{
            width: 50%;
            margin-top: 20px;
            border-radius: 15px;
}
.Sidebar{
    flex: 0.4 1;
}
.Sidebar__name{
    font-family: 'Kaushan Script';
}

.Sidebar__surname{
    color: #00f260
}
.Sidebar >h3{
    background-color: #f1f1f1;
    border-radius: 30px;
    padding: 10px;
    margin: 10px;
}
.Sidebar__download{
    margin-bottom:6px;
    padding-top:6px;
    height:40px;
    font-size:1.1rem;
    border-radius:30px;
    margin-right: 12px;
    margin-left: 12px;
    padding: 1px;
     
} 
a{
    color: inherit;

}

.Sidebar__download > sup{
    font-size: large;
    text-decoration: none;
}

.Sidebar__download > img{
    width: 40px;
    border-radius: 30px;
}
.Sidebar__download:hover{
    background-color: #f1f1f1;  
    cursor: pointer;
} 
.Sidebar__social{
    margin-top: 15px;  
}
.Sidebar__contact{
    margin: 10px 10px;
    background-color: #dbdddf;
    padding: 1px;
    border-radius: 20px;
    justify-content: center;
    
}


.Sidebar__contact> p{
    margin-right: 25px;
}
.Sidebar__github{
    padding: 8px;
    border-radius: 20px;
    margin-top: 10px;
}
.Sidebar__github:hover{
    background-color: #f1f1f1;
    cursor: pointer;
    
}
.Sidebar__email{
    padding:10px;
    background-color: #f1f1f1;
    border-radius: 50px;
    margin: 20px 30px;
    background-image:linear-gradient(to right bottom,#00f260,#0575e6);
    cursor:pointer;
    transition:all 2s linear;
}
.Sidebar__email:hover{transform: scale(1.2)}



/* About */
.About{
    padding: 70px;
    background-color: #dbdddf;
    margin-top: 110px;
    border-radius: 70px 0px 80px;
    margin-left: 30px;
    margin-right: 30px;
    font-size: 1rem;
    line-height: 1.5;
    font-weight: lighter;
    text-align: justify;
    font-family: 'Playfair Display', serif;
}
.bo{
    font-weight: bolder;
}

/* Navbar */
  .navbar{
    display: flex;
    margin-top: 50px;
    }

.navbar__active{
    font-size: 2.2rem;
    font-weight: 700;
    color: #354358;
    margin-left: 30px;
    
    border-bottom: 4px solid #00F260;
}

.navbar__item{
    margin-right: 20px;
    color: #354358;
    font-size: 1.3rem;
    font-family: 'Merriweather';
    cursor: pointer;
    font-weight: lighter;
}

.navbar__item:hover{
    background: -webkit-linear-gradient(#00f260 , #0575e6);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.navbar__title{
    display:flex;
    margin-left: 500px;
    
}
/* a:-webkit-any-link { color: white; }  */

/* Projects */
a { text-decoration: none; }

.projects__row{
    display: flex;
}
.projects_icon{
    margin: 40px 20px;
    width: 85%;
    height: 70%;
    min-height: 160px;
    border-radius: 20px;
    transition: transform 0.3s linear;
    border: 1px solid #00f260;
    
}
.projects_icon:hover {
    transform: scale(1.05);
}
span ~ span {
    padding: 0px 110px;
}
.projects__row2{
    display: flex;

}
.projects__row1_one, .projects__row1_two, .projects__row1_two{
    margin-left: 55px;
}



/* Resume */
.Resume>h1,h5,p{
    margin-left: 37px;
}
.row{
    display: flex;
    justify-content: space-evenly;
}

.Resume__lang{
   margin-right: 50px;
}
/* Left tools detail */
.bar__detail1,.bar__detail2,.bar__detail3{
    width: 150%;
    background-image: linear-gradient(90deg, #00F260 , #0575E6);
    color: white;
    border-radius: 20px;
    /* padding-right: 80px; */
    margin-top: 15px;
    text-align: inherit;
}
.bar__detail1{
    padding-right: 130px;
}
.bar__detail2{
    padding-right: 100px;
}
.bar__detail3{
    padding-right: 160px;    
}
.bar{
    width: 100px;
    background-color: #f1f2f3;
    border-radius: 20px;
    padding-right: 240px;
}
.Resume__lang > .bar{
    padding-right: 270px;
}












