@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);

/* About */
.About{
    padding: 70px;
    background-color: #dbdddf;
    margin-top: 110px;
    border-radius: 70px 0px 80px;
    margin-left: 30px;
    margin-right: 30px;
    font-size: 1rem;
    line-height: 1.5;
    font-weight: lighter;
    text-align: justify;
    font-family: 'Playfair Display', serif;
}
.bo{
    font-weight: bolder;
}

/* Navbar */
  .navbar{
    display: flex;
    margin-top: 50px;
    }

.navbar__active{
    font-size: 2.2rem;
    font-weight: 700;
    color: #354358;
    margin-left: 30px;
    
    border-bottom: 4px solid #00F260;
}

.navbar__item{
    margin-right: 20px;
    color: #354358;
    font-size: 1.3rem;
    font-family: 'Merriweather';
    cursor: pointer;
    font-weight: lighter;
}

.navbar__item:hover{
    background: -webkit-linear-gradient(#00f260 , #0575e6);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.navbar__title{
    display:flex;
    margin-left: 500px;
    
}
/* a:-webkit-any-link { color: white; }  */

/* Projects */
a { text-decoration: none; }

.projects__row{
    display: flex;
}
.projects_icon{
    margin: 40px 20px;
    width: 85%;
    height: 70%;
    min-height: 160px;
    border-radius: 20px;
    transition: transform 0.3s linear;
    border: 1px solid #00f260;
    
}
.projects_icon:hover {
    transform: scale(1.05);
}
span ~ span {
    padding: 0px 110px;
}
.projects__row2{
    display: flex;

}
.projects__row1_one, .projects__row1_two, .projects__row1_two{
    margin-left: 55px;
}



/* Resume */
.Resume>h1,h5,p{
    margin-left: 37px;
}
.row{
    display: flex;
    justify-content: space-evenly;
}

.Resume__lang{
   margin-right: 50px;
}
/* Left tools detail */
.bar__detail1,.bar__detail2,.bar__detail3{
    width: 150%;
    background-image: linear-gradient(90deg, #00F260 , #0575E6);
    color: white;
    border-radius: 20px;
    /* padding-right: 80px; */
    margin-top: 15px;
    text-align: inherit;
}
.bar__detail1{
    padding-right: 130px;
}
.bar__detail2{
    padding-right: 100px;
}
.bar__detail3{
    padding-right: 160px;    
}
.bar{
    width: 100px;
    background-color: #f1f2f3;
    border-radius: 20px;
    padding-right: 240px;
}
.Resume__lang > .bar{
    padding-right: 270px;
}












